import { Row, Col } from "react-bootstrap/esm";

import "../Assets/Styles/CodeDemo.css";

function CodeDemo(props) {
  return (
    <Row>
      <Col className="project-links">
        <a
          href={props.demo}
          target="_blank"
          rel="noreferrer"
          className="code-demo"
        >
          <i className="bi bi-display"></i>
          {props.small ? "" : " Demo"}
        </a>
        <a
          href={props.code}
          target="_blank"
          rel="noreferrer"
          className="code-demo"
        >
          <i className="bi bi-code"></i>
          {props.small ? "" : " Code"}
        </a>
      </Col>
    </Row>
  );
}

export default CodeDemo;
