import Col from "react-bootstrap/esm/Col";
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Card from "react-bootstrap/esm/Card";

import CodeDemo from "./CodeDemo";
import Tags from "./Tags";

import "../Assets/Styles/Projects.css";

import imgProject01 from "../Assets/Images/pic01.png";
import imgProject02 from "../Assets/Images/pic02.png";
import imgProject04 from "../Assets/Images/pic04.png";

import data from "../Assets/Data/dataProjects.json";

function Projects() {
  return (
    <Container id="projects">
      <Row>
        <Col lg={6} className="project">
          <Card className="bg-dark text-white project-1">
            <Card.Img
              src={imgProject01}
              alt="Card image"
              className="project-image"
            />
            <Card.ImgOverlay>
              <Card.Title>{data[0].title}</Card.Title>
              <Tags tags={data[0].tags}></Tags>
              <Card.Text>{data[0].description}</Card.Text>
              <CodeDemo demo={data[0].demo} code={data[0].code}></CodeDemo>
            </Card.ImgOverlay>
          </Card>
        </Col>
        <Col lg={6}>
          <Row>
            <Col md={12} className="project">
              <Card className="bg-dark text-white project-2">
                <Card.Img src={imgProject02} alt="Card image" />
                <Card.ImgOverlay>
                  <Card.Title>{data[1].title}</Card.Title>
                  <Tags tags={data[1].tags}></Tags>
                  <Card.Text>{data[1].description}</Card.Text>
                  <CodeDemo demo={data[1].demo} code={data[1].code}></CodeDemo>
                </Card.ImgOverlay>
              </Card>
            </Col>
            <Col md={12}>
              <Row>
                <Col md={6} className="project">
                  <Card className="bg-dark text-white project-3n4">
                    <Card.Img src={imgProject04} alt="Card image" />
                    <Card.ImgOverlay>
                      <Card.Title>{data[2].title}</Card.Title>
                      <Tags tags={data[2].tags}></Tags>
                      <CodeDemo
                        demo={data[2].demo}
                        code={data[2].code}
                        small={true}
                      ></CodeDemo>
                    </Card.ImgOverlay>
                  </Card>
                </Col>
                {/* <Col md={6} className="project">
                  <Card className="bg-dark text-white project-3n4">
                    <Card.Img src={img01} alt="Card image" />
                    <Card.ImgOverlay>
                      <Card.Title>Card title</Card.Title>
                      <Tags tags={["test1", "test2", "test3"]}></Tags>
                      <CodeDemo
                        demo="http://localhost:3000/demo"
                        code="http://localhost:3000/code"
                        small={true}
                      ></CodeDemo>
                    </Card.ImgOverlay>
                  </Card>
                </Col> */}
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
}

export default Projects;
